* {
  margin: 0;
  box-sizing: border-box;
}
html, body {
  width: 100vw;
  height: 100vh;

}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  font-size: 16px;
}

.form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* width: 60vw; */
  width: clamp(40vw, 70%, 600px);
  padding: 5%;
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.form__input {
  width: 70%;
  height: 40px;
  padding: 2px;
  border-color: transparent;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 1px 3px 3px 0 rgba(0, 0, 0, 0.19);
}

.form__input:focus-visible, textarea:focus-visible {
  outline: 1px solid grey;
}

label, input, textarea, select {
  margin-bottom: 0.8rem;
}

label {font-weight: 600;}
span {
  font-weight: 400;
}

select {
  padding: 5px;
  border: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 1px 3px 3px 0 rgba(0, 0, 0, 0.19);
  transition: all 0.2s ease-in-out;
}

textarea {
  width: 70%;
  border-color: transparent;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 3px 0 rgba(0, 0, 0, 0.19);
}

button {
  padding: 10px 25px;
  color: aliceblue;
  background-color: dodgerblue;
  border: none;
  border-radius: 5px;
}

button {
  margin-top: 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: royalblue;
}

.error {
  border-color: red;
}

.form__legend {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 1.5rem;
}

@media (width < 700px) {
  body {
   font-size: 14px; 
  }

  .form {
    width: 100vw;
    box-shadow: none;
  }

  option {
    font-size: 11px;
  }

  span {
    display: block;
  }
  
}